.article-list {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
    gap: 20px;
    padding: 40px;
    background-color: #f7f7f7;
}

.article-item {
    background-color: #fff;
    border-radius: 8px;
    overflow: hidden;
    display: flex;
    flex-direction: column;
    transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.article-item:hover {
    transform: translateY(-5px);
    box-shadow: 0 8px 16px rgba(0, 0, 0, 0.2);
}

.article-image {
    width: 100%;
    height: 200px;
    object-fit: cover;
}

.article-content {
    padding: 20px;
    flex-grow: 1;
}

.article-item h2 {
    font-size: 22px;
    margin-bottom: 10px;
    color: #333;
}

.article-item p {
    font-size: 16px;
    margin-bottom: 15px;
    color: #666;
}

.article-link {
    color: #ff5722;
    text-decoration: none;
    font-weight: bold;
}

.article-link:hover {
    text-decoration: underline;
}
