/* HomeLayout.css (ví dụ) */
.home-layout {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1.2fr; /* 3 cột trái = 1fr, cột phải = 1.2fr */
    gap: 20px;
    max-width: 1200px; /* Chiều rộng tối đa, tùy chỉnh */
    margin: 0 auto;    /* Căn giữa trang */
    box-sizing: border-box; /* Tránh padding gây nở khung */
    padding: 20px 0;  /* Kéo nội dung xuống một chút nếu muốn */
  }
  
  .left-col, .right-col {
    background-color: #f9f9f9;
    border-radius: 8px;
    padding: 15px;
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
  }
  

.right-col {
    background-color: #fafafa;
    border-radius: 8px;
    padding: 15px;
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
    /* Cấu hình style riêng cho cột phải (nếu muốn) */
}

@media (max-width: 1024px) {
    .home-layout {
        grid-template-columns: 1fr 1fr; 
    }
}

@media (max-width: 768px) {
    .home-layout {
        grid-template-columns: 1fr; 
    }
}
